import Vue from 'vue'
import VueRouter from 'vue-router'
import login from '@/views/Login/login.vue'
import home from '@/views/home.vue'
import register from '@/views/Login/register.vue'
import { Toast } from "vant";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/register',
    name: 'register',
    component: register
  },
  {
    path: '/home',
    name: 'home',
    component: home,
    meta:{
      root:true
    }
  },{
    path: '/my',
    name: 'my',
    component: ()=>import('@/views/user/my.vue'),
    meta:{
      root:true
    }
  },{
    path: '/public',
    name: 'public',
    component: ()=>import('@/views/public/public.vue'),
    meta:{
      root:true
    }
  },{
    path: '/sousuo',
    name: 'sousuo',
    component: ()=>import('@/views/sousuo/search.vue')
  },{
    path: '/mymsg',
    name: 'mymsg',
    component: ()=>import('@/views/user/mymsg.vue')
  },{
    path: '/listcontent',
    name: 'listcontent',
    component: ()=>import('@/views/listContent/index.vue')
  },
  {
    path: '/mypublic',
    name: 'mypublic',
    component: ()=>import('@/views/user/myPublic.vue')
  },
  {
    path: '/mychangemima',
    name: 'mychangemima',
    component: ()=>import('@/views/user/myChangeMima.vue')
  },
  {
    path: '/myshoucang',
    name: 'myshoucang',
    component: ()=>import('@/views/user/myShouCang.vue')
  },

  // 
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
})
let authPageList = ["/myshoucang", "/mychangemima",'/mypublic','/mymsg','/public',];
  router.beforeEach((to,from,next)=>{
    if (authPageList.includes(to.path)) {
      let uid=localStorage.getItem('uid')
      let token=localStorage.getItem('token')
      let tokenExpired=localStorage.getItem('tokenExpired')
      if (uid&&token) {
        let now = +new Date() ;
         if(now>=tokenExpired){
            Toast('你的账户长时间没登陆，请重新登录')
            setTimeout((v) => {
              router.push("/login");
            }, 600);  
         }
         next()
      }else{
         //  mei 登陆  让他跳去登陆
          Toast("您还未登录,请先登陆");
          setTimeout((v) => {
            router.push("/login");
          }, 600);
          }
    }else{
      next()
    }
    
   
  })
export default router
