<template>
  <div @touchmove="backTop">
     <!-- 头部 -->
    <div class="hone-top">
        <router-link to="/sousuo">
            <div class="search1">
                <van-icon name="search" />
                <span>搜索</span>
            </div>
        </router-link>
    </div>
    <!-- tab -->
    <div class="tab">
        <van-tabs swipeable> 
            <van-tab v-for="item in arr" :title="item.name" :name='item._id' :key="item._id" >
             <!-- content -->
                <lists-view  :id='item._id'></lists-view>
            </van-tab>
        </van-tabs>
    </div>
    <a href="#" v-show="isShow" @click="top" class="top">
      <van-icon name="back-top" size='25' class="topimg"/>
    </a>
    <!-- 尾部 -->
  </div>
</template>

<script>
import { Icon,Tab, Tabs} from 'vant';
import listsView from '@/views/list.vue'
import axios from 'axios'
// import bus from '@/views/busEvent/bus.vue'
export default {
 name:'home',
 components:{
    [Icon.name]:Icon,
    [Tab.name]:Tab,
    [Tabs.name]:Tabs,
   
    listsView
 },
 data() {
    return {
        arr:[],
        // list:[],
        isShow:false,
    }
 },
 methods:{
    backTop(e){
      if (e.view.pageYOffset >= 1500) {
        this.isShow = true;
        // console.log(1);
      }else{
         this.isShow = false
      }
      // console.log(e.view.pageYOffset);
    },
    top(){
      // console.log(document.documentElement.scrollTop);
      this.isShow = false
    }
  },
 mounted(){
    axios({
      method:'get',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list'
    }).then(res=>{
        this.arr=res.data.data
        // console.log(res);
    })
    // this.tablist();
 },
 
}
</script>

<style lang="less" scoped>
.hone-top{
    width: 100%;
    height: 50px;
    background-color: rgb(7,193,76);
    padding-top:13px ;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    .search1{
        width: 80%;
        margin: 0px auto;
        height: 40px;
        line-height: 40px;
        text-align: center;
        background-color: rgb(164, 216, 199);
        border-radius: 50px;
        color: white;
        span{
            margin-left: 5px;
        }
    }
}
.tab{
    margin-top: 65px;
    // width: 100%;
    // position: fixed;
    // top: 60px;
    // left: 0;
    // z-index: 111111;
    /deep/.van-tabs__line{
    background-color: rgb(7,193,76);
    
    }
    /deep/.van-tabs__wrap{
        height: 11.73333vw;
        position: fixed;
        top: 62px;
        left: 0;
        width: 100%;
        z-index: 9;
    }
}
</style>