<template>
  <div class="list-content">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" success-text="刷新成功">
        <van-list
            v-model="loading"
            :finished="finished"
            offset
            finished-text="没有更多了"
            @load="onLoad(id)"
            >
                <van-cell v-for="(item,i) in list" :key="i"  @click="jinruxq(item._id)">
                  <div class="content1" v-if="item.imageSrc.length>1">
                    <h3>{{item.title}}</h3>
                    <div class="content-img">
                        <div class="content-div" v-for="(item1,i) in item.imageSrc" :key="i">
                            <img :src="item1 | imgFilter" alt="" @error="imgError" v-lazy="item1"/>
                        </div>
                    </div>
                      
                  </div>
                  <div class="content" v-else>
                    <h3>{{item.title}}</h3>
                    <div class="content-img" v-for="(item1,i) in item.imageSrc" :key="i">
                        <img :src="item1 | imgFilter" @error="imgError" v-lazy="item1"/>
                    </div>
                  </div>
                  <div class="content-bottom">
                    <p class="author">作者{{item.author}}</p>
                    <p>{{item.comment}}评论</p>
                    <p>发布时间:{{ontime(item.time)}}</p>
                    <!-- new Date(item.time).toLocaleDateString() -->
                    <div>
                        <van-icon name="cross" />
                    </div>
                  </div>
                </van-cell>
          </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import axios from 'axios'
import config from '@/config/index.js'
import { Icon,Tab, Tabs,List,Cell,PullRefresh } from 'vant';
export default {
  components:{
    [Icon.name]:Icon,
    [Tab.name]:Tab,
    [Tabs.name]:Tabs,
    [List.name]:List,
    [Cell.name]:Cell,
    [PullRefresh.name]:PullRefresh 
 },
 data() {
  return {
        loading: false,
        finished: false,
        list:[],
        skip:0,
        refreshing: false,
        wzid:''
  }
 },
 props:['id'],
 methods:{
  onLoad(id) {
      // 异步更新数据 
      // console.log(id);
      this.wzid=id 
      axios({
            method:'post',
            url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_list',
            data:{
                    "cate_id":id?id:'610128169b33ed00018ed5df', 
                    "skip":this.list.length,
                    "limit":10
                }    
            }).then(res=>{
                this.list.push(...res.data.data)
                console.log("新闻列表",res);
                // 加载状态结束
                this.loading = false;
                this.refreshing=false;
                  // 数据全部加载完成
                if (this.list.length >=res.data.count){
                  this.finished = true;
                };
      }) 
        // console.log("123",this.skip);
        // this.skip+=10;
        

       
    },
    jinruxq(id){
      this.$router.push({
          name:'listcontent',
          params:{
            id:id
          }
      })
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.list=[];
      this.onLoad(this.wzid);
    },
    
 },
 filters:{
     imgFilter(img){
        if(img.includes(config.baseImgUrl) || img.includes(config.baseImgUrls)){
            return img+'?imageMogr2/thumbnail/!50p'
        }
        return img
     }
 },
}
</script>

<style lang="less" scoped>
.list-content{
  margin-bottom: 50px;
  .van-list{
    margin-top: 40px;
  }
}
.content{
    display: flex;
    justify-content: space-between;
    .content-img{
        width: 100px;
        img{
            width: 100%;
            height:auto;
            }
    }
   
}
.content1{
    
    .content-img{
        // width: 375px;
        display: flex;
        justify-content: space-around;
        .content-div{
            width: 100px;
            img{
                width: 100%;
                height:auto;
                
            }
        }
        
    }
   
}
.content-bottom{
    // height: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 8px;
    .author{
      width: 40vw;
      // overflow: hidden;
    }
}
</style>